.banner{
    background-image: url(./../../img/banner2.jpg);
    position: relative;
}
.banner .overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: #0c2738;
    opacity: .7;
}
.banner .banner-content{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    color: #FFF;
}