nav{
    background-color: #0c2738;
}

.navbar-brand {
    color: #FFF;
    font-size: 24px;
    font-weight: bold;
}
nav .nav-item{
    margin: auto 5px;
}
nav .nav-item .nav-link{
    color: #FFF;
    transition:all 0.5s ease-in-out;
}
nav .nav-item .nav-link:hover{
    color: aqua;
    border-bottom: 1px solid aqua;
    transform: scale(.9);
}

.nav-link-active{
    color: aqua;
    border-bottom: 1px solid aqua;
    transform: scale(.9);
}
.navbar-toggler{
    background-color:#164968 ;

}
nav .navbar-toggler-icon{
    background-color: #FFF;
    height: 5px;
    border-radius: 5px;
}