.DetailsCard, .card{
    background-color: #081b27;
    color: #FFF;
    border: none;
}
.DetailsCard .pic{
    height: 90vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}
.DetailsCard .card-body{
    border: 1px solid #f3ac0c;
    padding: 20px;
}
.DetailsCard .card-body h5{
    color: #f3ac0c;
}
.DetailsCard .head{
    text-shadow: 0px 0px 10px #f3ac0c;
    color: #FFF;
    text-transform: capitalize;
    padding-bottom: 40px;
}