.about{
    background-color: #081b27;
}
.about .head{
 color: #FFF;
 padding-top: 50px;
  
}
.about .rowTeam{
    justify-content: space-evenly;
    padding: 30px;
}
.about .card{
    text-align: center;
    margin: 20px auto;
    background-color: #0c2738;
    box-shadow: 0 0 10px 0 #FFF;

}
.about  .card .img-con{
    border: 20px solid rgba(0, 0, 0, 0.4);
    border-radius: 50%;
    width: 180px;
    height: 180px;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 20px;

}
.about .card-img-top{
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
}
.about .card .alaa{
    background-image : url(./../../img/avatar.jpg);
}

.about .card .mohamed{
    background-image : url(./../../img/elsisy.jpg);
}
.about .card .btn{
    color: #FFF;
    background-color: #164968;
    margin-top: 20px;
    transition: all 0.5s ease-in-out;
    box-shadow: 0 0 10px 0 #FFF;
    margin-bottom: 20px;
}
.about .card .btn:hover{
    background-color: #081b27;
}
.about .rates{
    background-color: #FFF;
    padding-bottom: 30px;
    
}
.about .rates .head{
    color: #081b27;
    margin-bottom: 20px;
}
.about .rates .rate{
    padding: 20px;
    border: 1px solid #164968;
    box-shadow: 0 0 10px 0 #164968;

    background-color: #081b27;
    margin: 20px;
    color: #FFF;
    border-radius: 15px;
}
.about .rates .rate .icon{
    font-size: 50px;
    margin: auto;
    margin-bottom: 20px;
    color: #f3ac0c;
}

.about .testimonials{
    background-color: #081b27;
}
.about .testimonials .carousel-item{
    height: 350px;
}
.about  .testimonials .person{
    border: 20px solid rgba(0, 0, 0, 0.4);
    border-radius: 50%;
    width: 180px;
    height: 180px;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 20px;

}
.about .testimonials .person-img{
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
}
.about .testimonials .one{
    background-image : url(data:image/jpeg;base64,);
}

.about .testimonials .two{
    background-image : url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQa4xjShh4ynJbrgYrW_aB4lhKSxeMzQ3cO_A&usqp=CAU);
}
.about .testimonials .three{
    background-image : url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSoKJPxxwPeNvISnBbZsZHe887Ws0FnrL7o0w&usqp=CAU);
}
.about .testimonials .person-caption{
    margin-top: 50px;
}