.footer{
    background-image: url(./../../img/footer.jpg);
    background-position: center;
    color: #FFF;
    padding: 20px;
    position: relative;
    padding-top: 50px;
}
.footer .overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-image: linear-gradient(#0a161c, #0c2738);
    opacity: .9;
}
.footer .container-fluid{
    position: relative;
    z-index: 2;
}

.footer ul .footer-link{
    margin: 5px auto;
    color: aqua;
    transition: all 0.5s ease-in-out;
    cursor: pointer;
    padding: 5px;
    transition: all 0.5s ease-in-out;
}
.footer ul .footer-link .nav-link{transition: all 0.5s ease-in-out;
padding-left: 0;}
.footer ul .footer-link .nav-link:hover{
    color: #f3ac0c;
    transform: scale(1.1);
}
.footer ul .footer-link i{
    color: #f3ac0c;
    font-size: 22px;
}
