.Favorites {
    background-color: #081b27;
}
.Favorites .myCard .card{
    position: relative;
}
.Favorites .myCard .delete{
    position: absolute;
    top: 0;
    right: 0;
    background-color: #0c2738;
    padding: 5px;
}
.Favorites .myCard .delete i {
    font-size: 20px;
    color: #f3ac0c;
}
.Favorites .head{
    text-shadow: 0px 0px 10px #f3ac0c;
    color: #FFF;
    text-transform: capitalize;
}