.myCard  .card{
    position: relative ;
    border: 1px solid #c69500;
    transition: all 0.5s ease-in-out;
    box-shadow: 0px 0px 7px #FFF;
    margin-bottom: 30px;
}
.myCard  .card:hover{
    transform: scale(1.05);
}
.myCard  .rate{
    position:absolute;
    top: 0;
    left: 0;
    background-color: #0c2738;
    padding: 5px;
    color: #f3ac0c;
    font-size: 16px;
    font-weight: 700;
}
.myCard   .star{
    position:absolute;
    top: 0;
    right: 0;
    background-color: #0c2738;
    padding: 5px;
}
.myCard  .star i{
    font-size: 25px;
    color: #f3ac0c
}
.myCard   .card .img-con{
    width: 100%;
    height:370px;
    margin: auto;
}
@media (max-width:768px) {
    .myCard   .card .img-con{
        height: 340px;
    }
}
.myCard .card-img-top{
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 100%;
}
.myCard  .card .card-body{
background-color: #f3ac0c;
text-align: center;
}
.myCard  .card-title{
    font-weight: bold;
    margin-bottom: 10px;
    font-size: 18px;
}
.myCard  .card .card-body .btn{
    color: #FFF;
    transition: all 0.5s ease-in-out;
    font-weight: 700;
    font-size: 15px;
}
.myCard  .card .card-body .btn:hover{
    background-color: #FFF;
    color: #0c2738;
}
.btn-general{
    background-color: #0c2738;
}